/* eslint-disable no-unused-expressions */
/* eslint-disable no-shadow */
/* eslint-disable @typescript-eslint/no-empty-function */
import { gql } from '@apollo/client';
import { AtomTextEditorHermer, AtomWrapper } from '@atoms';
import { FC } from 'react';

type PropsIndex = {
  propTest: string;
};

export const LIST_MEASURE_UNITIES = gql`
  query measureUnities($projectId: UUID!) {
    measureUnities(projectId: $projectId) {
      id
      name
      abbreviation
    }
  }
`;

const PageIndex: FC<PropsIndex> = () => {
  return (
    <AtomWrapper alignItems="center" margin="100px 0px">
      <AtomTextEditorHermer
        options={{
          notIncludeSelectFonts: true,
        }}
      />
    </AtomWrapper>
  );
};

export default PageIndex;
